
.background{
    width:100vw;
    min-height:100vh;
    background: rgb(0,0,0);
    position:absolute;
    top:0;
    left:0;
    overflow: hidden;
}
.homeFlexBox{
    display:flex;
    justify-content: space-around;
    align-items: center;
}

.img{
    width:45vw;
    height:50vw;
    animation:imageScale 1.5s forwards
}
.backgroundOpacity{
    position:absolute;
    top:0;
    right:0;
    z-index:5;
    width:50vw;
    min-height:100vh;
    background: rgb(0,0,0);
    background: linear-gradient(90deg, rgba(0,0,0,0) 36%, rgba(0,0,0,1) 71%);
    
}
.introduction{
    display:flex;
    flex-direction: column;
    width:40vw;
    height:100vh;
    justify-content: center;
    align-items: center;
    color:white;
}
.introduction h1{
    font-size:9vw;
    margin:1vw;
    font-family: 'Roboto', sans-serif;
    cursor:pointer;
    transition:0.2s;
}
.introduction .linkBlock:nth-child(1) a h1{
    animation:fadeIn 0.5s forwards;
}
.introduction .linkBlock:nth-child(2) a h1{
    animation:fadeIn2 1s forwards;
}
.introduction .linkBlock:nth-child(3) a h1{
    animation:fadeIn3 1.5s forwards;
}
a{
    text-decoration: none;
}
.introduction a{
    color:#7c7c7c;
}
.introduction .linkBlock:hover{
    transform:translate(5vw);
    transition: 0.5s;
}
.introduction .linkBlock:not(:hover){
    transform:translate(0vw);
    transition:0.5s;
    
}
.introduction .linkBlock:nth-child(1):hover a{
    color:#ffffff;
}
.introduction .linkBlock:nth-child(2):hover a{
    color:#ffffff;

}
.introduction .linkBlock:nth-child(3):hover a{
    color:#ffffff
}

.introduction .linkBlock{
    overflow: hidden;
    width:40vw;
    margin-left:10vw;
}

@keyframes fadeIn{
    0%{opacity:0}
    0%{transform:translate(0,100%)}
    100%{transform:translate(0)}
    100%{opacity:1}
}
@keyframes fadeIn2{
    0%{transform:translate(0,100%)}
    33%{opacity:0}
    33%{transform:translate(0,100%)}
    100%{transform:translate(0)}
    100%{opacity:1}
}
@keyframes fadeIn3{
    0%{transform:translate(0,100%)}
    66%{opacity:0}
    66%{transform:translate(0,100%)}
    100%{transform:translate(0)}
    100%{opacity:1}
}
@keyframes imageScale{
    0%{transform: scale(1.3)}
    0%{opacity:0}
    0%{right:10vw}
    100%{opacity:1}
    100%{right:5vw;}
    100%{transform:scale(1)}
}

@media only screen and (max-width:800px){
    .img{
        width:70vw;
        height:100vw;
        margin-right:-20vw;
    }
}
@media only screen and (max-width:600px){
    .img{
        display:none;
    }
    .introduction .linkBlock h1{
        font-size:15vw;

    }
}
@media only screen and (max-width:600px){
    .backgroundOpacity{
        display:none;
    }
    .img {
        display:none;
    }
    .linkBlock{
        min-width:80vw;
        
    }
    .introduction .linkBlock h1{
        font-size:20vw;

    }
}